const httpCoreUtils = require('./../../common/httpCoreUtils');
//import httpCoreUtils from './../../common/httpCoreUtils.js';
/*基础http通用接口*/
function getDolls(uid) {
	return httpCoreUtils.fetch('api/userDoll/getDolls',{userId:uid});
}



module.exports = {
  getDolls: getDolls,
}



